body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pokemon__search-filter {
width: 50%;
height: 40px;
padding-left: 10px;
border:0 ;
box-shadow: 0px 0px 12px 0px rgba(50, 50, 50, 0.26);
}
.pokemon__list-item {
width: 100%;
box-shadow: 0px 0px 12px 0px rgba(50, 50, 50, 0.26);
border: 1px solid #B6B6B6;
background-color: #FFFFFF;
}
.pokemon__card{
width: 100%;
display: flex;
flex-direction: column;
}
.pokemon__item{
background-color: #DEDEDE;
}
.pokemon__item-id{
margin: 0;
height: 18px;
background-color: #D0CDC9;
text-align: left;
margin-right: 80%;
padding: 5px 5px 0 5px;
color:#7A5F48;
font-size: 12px;
display: flex;
text-align: center;
}
.pokemon__info{
background-color: #FFFFFF;
display: flex;
flex-direction: column;
align-items: flex-start;
padding-left: 10px;
padding-bottom: 20px;
}
.pokemon__info-name{
text-transform: capitalize;
margin: 10px 0 2px 0;
}
.pokemon__info-skills{
width: 100%;
display: flex;
padding: 0;
}
.pokemon__info-skill{
border: 1px solid #B6B6B6;
border-radius: 5px;
list-style: none;
padding: 2px;
margin-right: 5px;
color:#907763;
font-size: 10px;
text-transform: uppercase;
}
.pokemon__list {
list-style: none;
padding: 0 15% 0 15%;
display: grid;
grid-template-columns: 1fr 1fr 1fr 1fr;
grid-template-rows: auto;
grid-gap: 20px;
margin-top: 30px; 
justify-items: center;
}
.pokemon__header{
position:fixed;
top:0;
width: 100%;
}
.left__triangle{
width: 0;
height: 0;
border-right: 35px solid transparent;
border-top: 35px solid black;
border-left: 35px solid black;
border-bottom: 35px solid transparent;
position: absolute;
top: 0;
left: 0;
}
.right__triangle{
width: 0;
height: 0;
border-right: 35px solid black;
border-top: 35px solid black;
border-left: 35px solid transparent;
border-bottom: 35px solid transparent;
position: absolute;
top: 0;
right: 0;
}
.pokemon__footer {
position:fixed;
bottom:0;
width: 100%;
}
.left__quarter{
width: 90px; 
height: 90px; 
background: #F1002F;
border-radius: 0 100% 0 0;
position: absolute;
bottom:0;
left:0;
}
.right__quarter{
width: 90px; 
height: 90px; 
background: #F1002F;
border-radius: 0 100% 0 0;
-webkit-transform: rotate(-90deg);
        transform: rotate(-90deg);
position: absolute;
bottom:0;
right:0;
}

.App {
display: flex;
flex-direction: column;
text-align: center;
background-color: #FBEE4C;
min-height: 100vh;
min-width: 100vh;
padding-top: 20px;
}
